ly ring ring-fuchsia-700/50; }\n  .debug > * { @apply ring-1 ring-fuchsia-900/75; }\n  .debug ~ .debug { @apply ring-pink-500/50; }\n  .debug ~ .debug > * { @apply ring-pink-700/50; }\n  .debug ~ .debug > * > * { @apply ring-pink-900/75; }\n}\n","@tailwind components;\n\n@layer components {\n  .onscreen-console {\n    @apply fixed top-0 inset-x-0 h-[50vh] overflow-x-hidden overflow-y-auto shadow-2xl;\n    @apply bg-stone-800/95 text-stone-400 text-sm flex flex-col-reverse;\n    @apply empty:before:content-['𝗖𝗼𝗻𝘀𝗼𝗹𝗲\\A𝘓𝘰𝘨_𝘮𝘦𝘴𝘴𝘢𝘨𝘦𝘴_𝘢𝘱𝘱𝘦𝘢𝘳_𝘩𝘦𝘳𝘦…'] empty:before:whitespace-pre empty:before:p-2;\n  }\n  .onscreen-console > * + * {\n    @apply border-t border-stone-400/25;\n  }\n  .onscreen-console > * {\n    @apply p-2 flex gap-2;\n  }\n  .onscreen-console > [data-msg-type] {\n    /* important for ⚠ and ℹ to render as emojis */\n    @apply font-serif;\n  }\n  .onscreen-console > [data-msg-type=error] {\n    @apply bg-red-500/25 text-red-500 before:content-['❌'];\n  }\n  .onscreen-console > [data-msg-type=warn] {\n    @apply bg-yellow-500/25 text-yellow-500 before:content-['⚠️'];\n  }\n  .onscreen-console > [data-msg-type=info] {\n    @apply before:content-['ℹ️'];\n  }\n  .onscreen-console > [data-msg-type=debug] {\n    @apply text-blue-500;\n  }\n  .onscreen-console > * > pre {\n    @apply font-mono whitespace-pre-wrap break-all;\n  }\n}\n","@import url(\"./utils.css\");\n@import url(\"./onscreen-console.css\");\n@tailwind base;\n@tailwind components;\n@tailwind utilities;\n\nhtml { @apply h-full bg-default text-default; }\nbody { @apply min-h-full overflow-y-auto; }\n\n/* pseudo-element to create a zero-height barrier at the top to avoid collapsing margins */\nbody::before { @apply content-[''] table h-0; }\n\n.app {\n  @apply sm:p-4;\n}\n\n.divider { @apply flex items-center justify-center w-48 mx-auto my-4 md:my-10;}\n.divider::before, .divider::after { @apply flex-1 content-[''] h-1 border-0 bg-default dark:bg-invert;}\n.divider::before { @apply rounded-l;}\n.divider::after { @apply rounded-r;}\n.divider:not(:empty)::before { @apply rounded-r mr-2;}\n.divider:not(:empty)::after { @apply rounded-l ml-2;}\n\n.divider-xs { @apply divider my-4 md:my-4 min-w-[50%] text-xs;}\n.divider-xs::before, .divider-xs::after { @apply h-[1px];}\n",null],"sourceRoot":""}]);
